
#list[data-v-74452fe3]{
	height: 100%;
	width: 20%;
  background-color: rgb(245,245,247);
  float: left;
  overflow-y: scroll;
}
dt[data-v-74452fe3],dd[data-v-74452fe3]{
  height: 30px;
  font-size: 15px;
  line-height: 30px;
  font-family: microsoft yahei;
  position: relative;
}
dd[data-v-74452fe3]{
  text-indent: 20px;
  opacity: 0.5;
}
dd[data-v-74452fe3]:hover{
  opacity: 1.0;
  cursor: pointer;
}
dt[data-v-74452fe3]{
  text-indent: 10px;
  color: rgb(125,125,125);
}
.icon[data-v-74452fe3]{
  width: 20px;
  height: 20px;
  vertical-align: top;
  margin-right: 10px;
  margin-top: 5px;
}
.choosed[data-v-74452fe3]{
  background-color: rgb(230,231,234);
  opacity: 1.0;
}
.choosed[data-v-74452fe3]:before{
  position: absolute;
  left: 0px;
  top: 0px;
  display: inline-block;
  content: ' ';
  width:5px;
  height: 30px;
  background-color: rgb(198,47,47);
}


/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
[data-v-74452fe3]::-webkit-scrollbar {
  width: 7px;
  background-color: #F5F5F5;
}
/*定义滚动条轨道 内阴影+圆角*/
[data-v-74452fe3]::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  border-radius: 10px;
  background-color: rgb(245,245,247);
}
/*定义滑块 内阴影+圆角*/
[data-v-74452fe3]::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgb(225,225,226);
  background-color: rgb(225,225,226);
}



