
.vue-slider-dot{
  background: url('../assets/point.png') no-repeat;
  box-shadow: none;
  border-radius: 0;
  position: relative;
}
.playprogress .vue-slider-dot{
  background-size: 5px 5px;
  background-position: 4px;
}
.volumeprogress .vue-slider-dot{
  background-size: 4px 4px;
  background-position: 3px;
}




